.ad-slider-container .carousel-control-prev,.ad-slider-container .carousel-control-next{height:30px;top:70px}.ad-slider-container .carousel-control-prev{left:-10px}@media all and (max-width: 414px){.ad-slider-container .carousel-control-prev{left:-25px}}.ad-slider-container .carousel-control-next{right:-10px}@media all and (max-width: 414px){.ad-slider-container .carousel-control-next{right:-25px}}.carousel-caption{position:relative;right:initial;bottom:initial;left:initial;z-index:initial;color:initial;text-align:initial}.carousel-caption img{width:100%}
